import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { FormGroup, Validators, FormBuilder } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';

import {  AlertController} from 'ionic-angular';

/**
 * The Welcome Page is a splash page that quickly describes the app,
 * and then directs the user to create an account or log in.
 * If you'd like to immediately put the user onto a login/signup page,
 * we recommend not using the Welcome page.
*/
@IonicPage()
@Component({
  selector: 'page-welcome',
  templateUrl: 'welcome.html'
})
export class WelcomePage {

 public form                   : FormGroup;
 public requestName         : any;
 public requestPassword         : any;
 
private baseURI               : string  = "https://qaprofil.com.qa/ionic/";

   constructor(public navCtrl    : NavController,
               public http       : HttpClient,
               public NP         : NavParams,
               public fb         : FormBuilder,
                public alertCtrl: AlertController,
                 
               public toastCtrl  : ToastController)   {
               
               // Create form builder validation rules
      this.form = fb.group({
         "username"                  : ["", Validators.required],
         "password"                  : ["", Validators.required],
         
      });
             
               }
 

 signIn (name : string, password : string) : void
   {
      let headers 	: any		= new HttpHeaders({ 'Content-Type': 'application/json' }),
          options 	: any		= { "key" : "create", "name" : name, "password" : password },
          url       : any      	= this.baseURI + "manage-data.php";

 



      this.http.post(url, JSON.stringify(options), headers)
      
      .subscribe((i : any) => 
      {
      



         console.dir(i);
         
         if(i>0){
       
          
    let alert = this.alertCtrl.create({
      title:'CONGRATS'   ,
      subTitle:("Your Login success"),
      buttons: ['OK']
      });
   
      alert.present();
      this.navCtrl.push('LoginPage',{name : name});
      
       

  }
         
  else
  {
  let alert = this.alertCtrl.create({
      title:"Error",
      subTitle:(i),
      buttons: ['OK']
      });
     
      alert.present();
      
  }
         
         
         
         
      },
      (error : any) =>
      {
         console.dir(error);
      });
    
   }

 addEntry() : void
   {
      this.navCtrl.push('MessagePage');
   }
 
   
 register() : void
   {
      this.navCtrl.push('RegisterPage');
   }
}



